@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.App {
  text-align: center;
}
.container{
  width: 100vw;
  height: 100vh;
  overflow-y: scroll;
}

.container::-webkit-scrollbar {
   display: none;  /* Safari and Chrome browsers */
}

.container {
   -ms-overflow-style: none; /* IE and Edge */
   scrollbar-width: none;  /* mozilla */
}

.image-grid {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 16px;
  gap: 16px; /* Adjust the gap between images as needed */
}

.image-grid-item {
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: calc(25% - 16px); /* Adjust the width of each item based on the desired number of columns and gap */
  margin-bottom: 16px;
}

.image-grid-item img {
  max-width: 100%;
  height: auto;
  display: block;
}




/* Style your components */
.populardesign_container{
    margin: 4rem 6rem;
}
.populardesign_container h1{
    font-size: 3rem;
}
.first-des{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 4.5rem;

}
.first-des-reverse{
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    margin-top: 8.5rem;

}
.des-text{
    width: 45%;
    text-align: start;
    font-size: 1.1rem;
}
.des-text h2{
    padding-bottom: 1rem;
}
.image{
    position: relative;
    width: 50%;
    display: flex;
    justify-content: space-between;
    z-index: -99;
}
.image img{
    width: 49%;
    height: 350px;
    object-fit: cover;
    border-radius: 6px;
    box-shadow: -1px 1px 62px -18px rgba(0, 0, 0, 0.19);
}
.image img:nth-child(2){
    position: absolute;
    top: -10%;
    right:0;
}

@media screen and (max-width:850px) {

   .populardesign_container{
    margin: 4rem 2rem;
} 
.first-des{
    display: flex;
    flex-direction: column-reverse;
    margin-top: 2.5rem;
}
.first-des-reverse{
    display: flex;
    flex-direction: column-reverse;
    margin-top: 2.5rem;
}
.des-text{
    width: 100%;
    
}
.image{
    width: 100%;
    margin: 1.8rem 0;
}
.image img{
   height: 250px;
}
}
.footer{
    padding: 2rem 6rem;
    background-color: black;
    color: white;
}
.top{
    display: flex;
    align-items: center;
    text-align: start;
    justify-content: space-between;
    flex-wrap: wrap;
}
.top i{
    font-size: 2rem;
    color: white;
    margin-left: 1rem;
}
.top i:hover{
    color: #01959a;
}
.top div p{
    font-size: 1.2rem;
}
.top div h4{
    font-size: 2.2rem;
}
.bottom{
    padding-top: 2rem;
    text-align: start;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.bottom div{
    display: flex;
    flex-direction: column;
}
.bottom h4{
    font-size: 1.3rem;
    padding: 1rem 0 0.8rem 0;
}
.bottom a{
    text-decoration: none;
    color: white;
    padding-bottom: 0.4rem;
    font-size: 1.1rem;
}
.footer_end{
    margin-top: 40px;
    margin-bottom: 0px;
}

@media screen and (max-width:850px) {
    .footer{
        padding: 4rem 2rem;
    }    
    .footer i{
        margin: 1rem 1rem 0 0;
    }
    .bottom div{
        width: 50%;
    }
}

/* Style your components */
.Main_section{
    width: 100%;
    height: 100vh;
    position: relative;
}
.Main_section-mid{
    width: 100%;
    height: 80vh;   /*vh make the height responsive*/
    position: relative;
}
/* to make the image responsive or to remove down bar in the window*/
img{
    width: 100%;
    height: 100%; }
.Main_sectiontext{
    position: absolute;
    top: 55%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    width: 100%;  /*make the width of the text to 100% */
}
.Main_sectiontext h1{
    font-size: 3rem;
    font-weight: 800;
    background: white;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.Main_sectiontext p{
    font-size: 1.3rem;
    color: white;
    padding: 0.5rem 0 2rem 0;
}
.Main_sectiontext .show{
    text-decoration: none;
    background: white;
    padding: 1rem 2rem;
    border-radius: 6px;
    font-size: 1.1rem;
    font-weight: bold;
    letter-spacing: 2px;
    color: black;
}
@media screen and (max-width:650px) {
    .Main_sectiontext h1{
        padding: 10px 20px;
    }
    .Main_sectiontext p{
        font-size: 1.1rem;
        padding: 0 0 2rem 0;
    }
    .Main_sectiontext .show{
        padding: 0.6rem 1.1rem;
        border-radius: 6px;
        font-size: 1rem;
        background-color: red;
    }
}



.Recent_design_cont{
    margin: 4rem 6rem;
    color: balck;
}
.Recent_design_cont h1{
    font-size: 3rem ;
}
.Recent_design_card{
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
}
.t-card{
    width: 32%;
    text-align: start;
    box-shadow: 0 5px 25px 2px rgba(0, 0, 0, 0.11);
    border-radius: 7px;
    cursor: pointer;
    padding: 1rem 0.8rem;
}
.t-image{
    height: 250px;
    overflow: hidden;
    border-radius: 7px;
}
.t-image img{
    width: 100%;
    height: 100vh;
    border-radius: 7px;
    transition: 0.3 ease-in-out;
}
.t-image:hover img{
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}
.t-card h4{
    font-size: 1.3rem;
    padding: 0.9rem 0 0.2rem 0;
}
@media screen and (max-width:850px){
    .Recent_design_cont{
        margin: 4rem 2rem;
    }
    .Recent_design_card{
        flex-direction: column;
    }
    .t-card{
        width: 100%;
        margin-bottom: 1.5rem;
    }
    
}
/* Style your components */
.NavbarItems{
    background-color: white;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0 30px;
    box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.25);
    width: 95%;
    height: 85px;
    border-radius: 13px;
    position: fixed;    /*to make the navbar sticky*/
    top: 20px;
    left: 50%; 
    -webkit-transform: translate(-50%); 
            transform: translate(-50%);
    z-index: 9999;  /*to make it at top we will give high priority*/
    
}
.navbar-logo{
    font-size: 2rem;
    cursor: pointer;
}
.nav-menu{
    display: grid;
    grid-template-columns: repeat(6,auto);
    grid-gap: 10px;
    list-style: none;
    align-items: center;
}
.nav-link{
    text-decoration: none;
    color: #222;
    font-size: 1.2rem;
    font-weight: 600;
    padding: 0.7rem 1rem;
    white-space: nowrap;        /* for making any hting in one row we do white sapce nowrap */

}
.nav-link i{
    padding-right: 10px;
}
.nav-link:hover{
  background-color: #01959a;
  color: white;
  border-radius: 4px;
  transition: all 0.2s ease-in-out;  
}
.nav-link-btn{
    display: none;
}
button{
    padding: 0.5rem 1rem;
    white-space: nowrap;
    border-radius: 0.3rem;
    font-size: 1.2rem;
    font-weight: 600;
    border: none;
    cursor: pointer;
    transition: 0.2s ease-in-out;
}
button:hover{
    background-color: black;
    color: white;
}
.menu-icon{
    display: none;
}


@media screen and (max-width: 850px) {
    .NavbarItems{
        z-index: 99;
    }
    .nav-menu{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        background-color: white;
        width: 100%;
        height: auto;
        position: absolute;
        top: 0;
        left: -100%;
        opacity: 0;
        align-items: stretch;
        padding: 80px 0 30px 0;
        margin: 0;
        transition: 0.3s ease-in-out;
        border-radius: 13px;
       
        
        

    }
    .nav-menu.active{
        left: 0;
        opacity: 1;
        z-index: -1;
        transition: 0.3s ease-in-out;

    }
    .nav-link{
        display: block;
        width: 100%;
        font-size: 1.2rem;
        padding: 2rem 0;
    }
    .nav-link:hover{
        background: #01959a;
        transition: none;
    }
    button{
        display: none;
    }
    .nav-link-btn{
        display: block;
        text-align: center;
        padding: 1.5rem;
        margin: auto;
        border-radius: 4px;
        width: 80%;
        background: #01959a;
        text-decoration: none;
        color: white;
        font-size: 1.2rem;
        font-weight: 700;
        letter-spacing: 2px;


    }
    .menu-icon{
        display: block;
        cursor: pointer;
    }
    .menu-icon i{
        font-size: 1.2rem;
        color: black;
    }
}
