.Recent_design_cont{
    margin: 4rem 6rem;
    color: balck;
}
.Recent_design_cont h1{
    font-size: 3rem ;
}
.Recent_design_card{
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
}
.t-card{
    width: 32%;
    text-align: start;
    box-shadow: 0 5px 25px 2px rgba(0, 0, 0, 0.11);
    border-radius: 7px;
    cursor: pointer;
    padding: 1rem 0.8rem;
}
.t-image{
    height: 250px;
    overflow: hidden;
    border-radius: 7px;
}
.t-image img{
    width: 100%;
    height: 100vh;
    border-radius: 7px;
    transition: 0.3 ease-in-out;
}
.t-image:hover img{
    transform: scale(1.3);
}
.t-card h4{
    font-size: 1.3rem;
    padding: 0.9rem 0 0.2rem 0;
}
@media screen and (max-width:850px){
    .Recent_design_cont{
        margin: 4rem 2rem;
    }
    .Recent_design_card{
        flex-direction: column;
    }
    .t-card{
        width: 100%;
        margin-bottom: 1.5rem;
    }
    
}